.newsletter-wrapper {
    margin-top: 8rem;
    margin-bottom: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.newsletter-container {
    width: 1100px;
    height: 380px;
    background: var(--color-footer);
    border-radius: 22px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ppp {
    position: absolute;
    font-family: var(--font-family-main);
    font-size: 1rem;
    font-weight: 500;
    top: 47%;
    left: 15%;
    z-index: 3;
}

.bbb {
    position: absolute;
    top: 30%;
    left: 15%;
    font-family: var(--font-family-main);
    font-size: 2.2rem;
    font-weight: 700;
    z-index: 3;
}

.button-id-text {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 1rem;
}

.subscribe-button {
    position: absolute;
    top: 60%;
    left: 15%;
}

.p-button {
    font-family: var(--font-family-main);
    font-size: 1rem;
    font-weight: 500;
}

.iphone-sub {
    position: absolute;
    left: 70%;
    top: 10%;
    object-fit: cover;
    height: 20rem;
    width: 10rem;
}

@media  screen and (max-width: 750px) {
    .iphone-sub {
        display: none;
    }

    .bbb {
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.2rem;
        margin-bottom: 1rem;
    }
    .ppp {
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 0.8rem;
        margin-bottom: 1rem;
    }
}