.main-wrapper {
    margin-top: 4rem;
    margin-bottom: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid {
    display: inline-block;
    justify-content: center;
    align-items: center;
}

.choice-card {
    margin: 1rem 0.5rem 1rem 0.5rem;
    display: inline-block;
    position: relative;
    height: 240px;
    width: 350px;
    background-color: var(--color-footer);
    border-radius: 1rem;
}

.choice-card h1 {
    text-align: start;
    font-family: var(--font-family-main);
    font-weight: 700;
    font-size: 2.2rem;
}

.choice-card p {
    text-align: start;
    font-family: var(--font-family-main);
    font-weight: 500;
    font-size: 1rem;
    color: var(--color-bg);
}

.choice-img {
    position: absolute;
    height: 12rem;
    width: 6rem;
    object-fit: cover;
    top: 10%;
    left: 10%;
}
.choice-img-2 {
    position: absolute;
    height: 12rem;
    width: 8rem;
    object-fit: cover;
    top: 10%;
    left: 6%;
}


.text-absolut {
    position: absolute;
    top: 38%;
    right: 0%;
    width: 50%;
}

.text-absolut-h1{
    position: absolute;
    top: 18%;
    right: 0%;
    width: 50%;
}

@media  screen and (max-width: 1050px) {
    .main-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .grid {
        display: inline-block;
        justify-content: center;
        align-items: center;
    }
    
    .choice-card {
        margin: 1rem 1rem 1rem 1rem;
        display: inline-block;
        position: relative;
        height: 240px;
        width: 350px;
        background-color: var(--color-footer);
        border-radius: 1rem;
    }
}

@media  screen and (max-width: 750px) {
    .main-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .grid {
        display: inline-block;
        justify-content: center;
        align-items: center;
    }
    
    .choice-card {
        display: flex;
        position: relative;
        height: 240px;
        width: 350px;
        background-color: var(--color-footer);
        border-radius: 1rem;
    }
}

