.reviews-wrapper {
    margin-top: 12rem;
    margin-bottom: 10rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.reviews-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid-3 {
    position: relative;
    display: inline-block;
    justify-content: center;
    align-items: center;
    max-width: 1150px;
}

.review-card {
    position: relative;
    width: 530px;
    height: 240px;
    background-color: var(--color-footer);
    border-radius: 22px;
    display: inline-block;
    justify-content: center;
    align-items: center;
    margin: 1rem 1rem 1rem 1rem;
}

.review-header {
    position: absolute;
    font-family: var(--font-family-main);
    font-weight: 700;
    font-size: 24px;
    top: 25%;
    left: 40%;
    transform: translate(0%, -50%);
}

.review-text {
    position: absolute;
    font-family: var(--font-family-main);
    font-weight: 500;
    font-size: 1rem;
    line-height: 19px;
    color: var(--color-bg);
    width: 260px;
    top: 50%;
    left: 40%;
    transform: translate(0%, -50%);

}

.portrait {
    position: absolute;
    height: 180px;
    width: 160px;
    object-fit: cover;
    border-radius: 15px;
    top: 50%;
    left: 20%;
    transform: translate(-50%, -50%);
}

.icon-stars {
    height: 1rem;
    width: 1rem;
}

.star-div {
    position: absolute;
    top: 75%;
    left: 40%;
    transform: translate(0%, -50%);

}

.small-title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--font-family-main);
    font-weight: 500;
    font-size: 1rem;
    color: var(--color-blog);
}

.big-title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--font-family-main);
    font-weight: 700;
    font-size: 2.2rem;
    color: var(--color-blog);
    margin-bottom: 2rem;
}

.slider {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 134px;
    position: absolute;
    top: 110%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
}

.circle-icon {
    width: 1rem;
    height: 1rem;
    cursor: pointer;
}

@media  screen and (max-width: 750px) {
    
    .grid-3 {
        margin-top: 10rem;
        display: inline-block;
        justify-content: center;
        align-items: center;
    }
    
    .review-card {
        width: 400px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .big-title {
        font-weight: 700;
        font-size: 1.8rem;
    }
    .portrait {
        height: 150px;
        width: 120px;
        top: 50%;
        left: 23%;
    }
}