body {
    background-color: white;
}

.brand-wrapper {
    margin-top: 8rem;
    display: flex;
    justify-content: center;
    z-index: 1;
}

.brand-container {
    display: flex;
    width: 70%;
    max-height: 450px;
    justify-content: flex-end;
}


.brand-text-container {
    width: 50%;
    height: 100%;
}

.brand-img-container {
    justify-content: center;
    align-items: center;
    display: flex;
    width: 50%;
    height: 100%;
}

.brand-img-box {
    justify-content: start;
    align-items: center;
    display: flex;
    width: 50%;
    height: 100%;
}
.brand-img {
    width: 20rem;
    height: 36rem;
    object-fit: cover;
}

.brand-text {
    min-width: 200px;
    max-width: 450px;
}

.search-button {
    width: 6rem;
    height: 2.5rem;
    background: var(--color-subtext);
    border: none;
    border-radius: 8px;
    position: absolute;
    right: 0.4rem;
    cursor: pointer;
}

.brand-button-container {
    justify-content: center;
    align-items: center;
    display: flex;
    width: 80%;
}

.brand-button {
    position: relative;
    display: flex;
    width: 28rem;
    height: 3.2rem;
    border-radius: 0.6rem;
    border: none;
    background-color: var(--color-text);
    justify-content: center;
    align-items: center;
}

.icon-search {
    position: absolute;
    width: 1.8rem;
    height: 1.8rem;
    left: 2%;
}

.button-input {
    width: 20rem;
    height: 2rem;
    border: none;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 0.8rem;
}

.search-button p{
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 1rem;
    color: var(--color-text);
}

.brand-text h1{
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 4rem;
    line-height: 80px;
    text-align: start;
    color: var(--color-text);
    margin-bottom: 1rem;
}

.brand-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1rem;
    line-height: 20px;
    text-align: start;
    color: var(--color-text);
    margin-bottom: 2rem;
}

.yes {
    display: block;
}

@media  screen and (max-width: 850px) {
    .brand-img-container {
        display: none;
    }
    .brand-container {
        display: block;
    }

}

@media  screen and (max-width: 650px) {
    .brand-img-container {
        display: none;
        margin-top: 14rem;
    }
    .brand-container {
        display: block;
    }
    .search-button {
        width: 3rem;
        height: 2.5rem;
        border-radius: 8px;
        right: 0.4rem;
    }
    
    .brand-button-container {
        width: 80%;
    }
    
    .brand-button {
        width: 18rem;
        height: 3.2rem;
        border-radius: 0.6rem;
    }
    
    .icon-search {
        position: absolute;
        width: 1.8rem;
        height: 1.8rem;
        left: 2%;
    }
    
    .button-input {
        width: 10rem;
        height: 2rem;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 0.8rem;
        outline: none;
    }
    
    .search-button p{
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 0.6rem;
        color: var(--color-text);

    }
}

@media  screen and (max-width: 360px) {
    .brand-img-container {
        display: none;
    }

    .brand-container {
        display: block;
    }

}