* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}
  
    body {
    background: var(--color-bg);
}

 .gradient__bg {

    height: 50rem;
    /* ff 3.6+ */
    background:-moz-radial-gradient(ellipse at 1% 0%, rgba(56, 54, 56, 1) 0%, rgba(37, 36, 37, 1) 13%, rgba(11, 11, 11, 1) 35%, rgba(11, 11, 11, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(ellipse at 1% 0%, rgba(56, 54, 56, 1) 0%, rgba(37, 36, 37, 1) 13%, rgba(11, 11, 11, 1) 35%, rgba(11, 11, 11, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(ellipse at 1% 0%, rgba(56, 54, 56, 1) 0%, rgba(37, 36, 37, 1) 13%, rgba(11, 11, 11, 1) 35%, rgba(11, 11, 11, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(ellipse at 1% 0%, rgba(56, 54, 56, 1) 0%, rgba(37, 36, 37, 1) 13%, rgba(11, 11, 11, 1) 35%, rgba(11, 11, 11, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(ellipse at 1% 0%, rgba(56, 54, 56, 1) 0%, rgba(37, 36, 37, 1) 13%, rgba(11, 11, 11, 1) 35%, rgba(11, 11, 11, 1) 100%);

}
            
.main__bg {
    background: var(--color-text);
}    

.end__bg {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 4rem;
    background: var(--color-blog);
}    

.end-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.end-wrapper p{
    color: var(--color-text);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1rem;
}