.navbar-wrapper {
    height: 5rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70%;
}

.navbar-section-left {
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.navbar-section-left h1 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1rem;
    line-height: 24px;
    color: var(--color-text);
}

.navbar-section-middle {
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
}


.navbar-section-middle-list {
    display: flex;
    flex-direction: row;
    list-style: none;
    align-items: center;
    justify-content: center;
    width: 100%;
    
}

.nav-middle-item-special {
    text-decoration: none;
    justify-content: center;
    display: flex;
    width: 10%;
}

.nav-middle-item {
    text-decoration: none;
    justify-content: center;
    display: flex;
    width: 22%;
}

.nav-middle-item p, a{
    font-weight: 500;
    font-size: 1rem;
    line-height: 24px;
    font-family: var(--font-family);
    color: rgb(139, 142, 153);
    text-decoration: none;
}

.navbar-section-right {
    width: 20%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin: 0.4rem 0 0.4rem;

}

.icon {
    margin-right: 10px;
    margin-left: 10px;
}

.media-navbar-menu {
    display: none;
    position: absolute;
    right: 2%;
}

.navbar-menu_container-links {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    position: absolute;
    cursor: pointer;
    top: 3rem;
    right: 14%;

}

.RiCloseLine , .RiMenu3Line {
    cursor: pointer;
}

@media  screen and (max-width: 850px) {
    .navbar-section-middle, .navbar-section-middle-list {
        display: none;
    }
    .navbar-section-left {
        position: absolute;
        left: 2%;
    }
    .navbar-section-right {
        position: absolute;
        left: 50%;
        transform: translate(-50%);
    }
    .media-navbar-menu {
        display: flex;
    }
    .navbar-container {
        width: 90%;
    }
}

@media  screen and (max-width: 650px) {
    .navbar-section-middle, .navbar-section-middle-list {
        display: none;
    }
    .navbar-section-right {
        position: absolute;
        left: 50%;
        transform: translate(-50%);
    }
    .media-navbar-menu {
        display: flex;
    }
}

@media  screen and (max-width: 360px) {s
    .navbar-section-middle, .navbar-section-middle-list {
        display: none;
    }
    .navbar-section-left {
        display: none;
    }
    .navbar-section-right {
        position: absolute;
        left: 50%;
    }
    .media-navbar-menu {
        display: flex;
    }
    .navbar-container {
        width: 90%;
    }
}


.navbar-menu_container-links p{
    color: var(--color-text);
    margin-bottom: 0.2rem;
    border-bottom: 2px solid var(--color-text);
}
