.article-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.article-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid-2 {
    position: relative;
    display: inline-block;
    justify-content: center;
    align-items: center;
    max-width: 1150px;
}

.product-card-overlay {
    display: inline-block;
    justify-content: center;
    align-items: center;
    height: 440px;
    width: 340px;
    margin: 1rem 1rem 1rem 1rem;
}

.product-card {
    margin: 1rem 1rem 1rem 1rem;
    display: inline-block;
    position: relative;
    height: 340px;
    width: 340px;
    background-color: var(--color-footer);
}

.product-card h1 {
    text-align: center;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 24px;
}

.product-card p {
    text-align: start;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 24px;
    color: var(--color-blog);
}

.product-img {
    position: absolute;
    height: 20rem;
    width: 12rem;
    object-fit: cover;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
}
.product-img-2 {
    position: absolute;
    height: 22rem;
    width: 16rem;
    object-fit: cover;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
}

.product-name {
    position: absolute;
    top: 110%;
    right: 50%;
    transform: translate(50%, -50%);
    width: 340px;
}

.product-cost{
    position: absolute;
    top: 125%;
    right: 50%;
    transform: translate(50%, -50%);
}

.icon-rating {
    height: 1rem;
    width: 1rem;
}

.stars {
    top: 117.5%;
    right: 50%;
    transform: translate(50%, -50%);
    position: absolute;
}

.new-title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--font-family-main);
    font-size: 2.2rem;
    text-align: center;
    font-weight: 700;
    margin-bottom: 2rem;
}

.new-title-small {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--font-family-main);
    font-size: 1rem;
    text-align: center;
    font-weight: 500;
    margin-top: 2rem;
}

.view-more-button {
    position: absolute;
    height: 46px;
    width: 196px;
    border-radius: 15px;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 1rem;
    text-align: center;
    border: none;
    background: var(--color-blog);
    color: white;
    cursor: pointer;
    transition: 0.1s;
    transform: translate(-50%, -50%);
    top: 110%;
    left: 50%;
    z-index: 3;
}

.view-more-button:active {
    color: black;
    background: rgb(165, 165, 165);
}

@media  screen and (max-width: 750px) {
    
    .grid-2 {
        display: inline-block;
        justify-content: center;
        align-items: center;
    }
    
    .product-card-overlay {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 440px;
        width: 340px;
        margin: 1rem 1rem 1rem 1rem;
    }
    
    .view-more-button {
        top: 105%;
    }
}